import { Component } from '@angular/core';
import { IonicPage, NavController, AlertController } from 'ionic-angular';
import { AngularFireList, AngularFireObject, AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';
import { ServiceProvider } from '../../providers/service/service'
import { AngularFireAuth } from 'angularfire2/auth';
import * as firebase from 'firebase/app';
// import { take } from 'rxjs/add/operator/take';

@IonicPage()
@Component({
  selector: 'page-jugar',
  templateUrl: 'jugar.html',
})
export class JugarPage {

  negociosRef: AngularFireList<any>;
  usuariosRef: AngularFireObject<any>;
  negocios: Observable<any[]>;
  usuario: Observable<any[]>;
  constructor(
    public navCtrl: NavController,
    public alertCtrl: AlertController,
    public database: AngularFireDatabase,
    public servicio: ServiceProvider, public afAuth: AngularFireAuth,
  ) {



    this.negociosRef = this.database.list('clicks/negocios');
    this.negocios = this.negociosRef.valueChanges();

  }

  createTask() {
    let newTaskModal = this.alertCtrl.create({
      title: 'New Task',
      message: "Enter a title for your new task",
      inputs: [
        {
          name: 'title',
          placeholder: 'Title'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Save',
          handler: data => {
            this.negociosRef.push({
              title: data.title,
              done: false
            });
          }
        }
      ]
    });
    newTaskModal.present(newTaskModal);
  }

  sumarPuntos() {
    if (this.afAuth.authState) {
      // this.usuariosRef = this.database.object('clicks/usuarios/' + this.afAuth.auth.currentUser.uid);
      // //  console.log(data);
      // this.usuario = this.usuariosRef.valueChanges();
      // this.usuariosRef.update(this.afAuth.auth.currentUser.uid, {
      //   puntos: element.puntos + 1
      // });
      var nuevoValor;
      this.usuariosRef = this.database.object('clicks/usuarios/' + this.afAuth.auth.currentUser.uid);
      this.usuariosRef.snapshotChanges().subscribe(action => {
    nuevoValor = action.payload.val().puntos + 10;
      
      });
  this.usuariosRef.update({ puntos: nuevoValor });

    }
  }

  // this.afAuth.auth.currentUser.uid
  login() {
    this.servicio.login();
  }

  logout() {
    this.servicio.logout();
  }
  removeTask(task) {
    console.log(task);
    this.negociosRef.remove(task.key);
  }
}


